@import "../../global.scss";
.menu{
    width: 300px;
    height: 100vh;
    background-color: $mainColour;;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
    &.active{
        right: 0;
    }
    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 80px;
        font-weight: 800;
        
        color: $mainFontColour;

        width: 60%;
        li{
            margin-bottom: 25px;
            a{
                font-size: 20px;
                color:inherit;
                text-decoration: none;
            }

            &:hover{
                font-weight: 600;

            }
        }
    }
}