.intro{
    width: 100vw;
    height: calc(100vh - 60px);
    background-color: rgb(141, 141, 141);
    display: flex;
    
    .left{
        flex: 0.5;
        overflow: hidden;

        display: flex;
        

        justify-content: center;
        align-items: center;
        flex-direction: column;
        


     



        .imageContainer{


            border-width: 55px;
            border-color: rgb(0, 0, 0);
            border-style: solid;
           
            background-color: rgb(42, 42, 42);
            margin: 0px 0px 0px 5%;
            

            

            
            width: 500px;
            height: 500px; 
            display: flex;
            align-items: bottom;
            justify-content: center;
            float: top;
            img{
                bottom: -30px;
                max-width:800px;
                max-height:800px;
                width: auto;
                height: auto;
            }

        }


    }
    .right{
        position: relative;
        flex: 0.5;

        .wrapper{
            border: 150px;
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            h1{
                margin: 10px 0;
                font-size: 60px;

            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
            }

        }
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;
            .icon{
                font-size: 80px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink {
                100%{
                    opacity: 0;

                }
                
            }

        }


    }

}